/*
The three strokes
*/

[class*="entypo-"]:before {
	font-family: 'entypo', sans-serif;
}

.nav {
    width: 80px;
	height: 100vh;
	background: #fff;
	position: fixed;
	left: 0;
	border-right: 2px solid #f0f0f0;
	z-index: 9999;
}

.logo-menu {
	cursor: pointer;
	display: block;
	margin: 47vh 0;
	cursor: pointer;
}

.logo-menu svg {
	display: block;
	margin: 0 auto;
	fill: #000000;
}

.nav-menu-shelf {
	animation: shelf 0.5s forwards linear alternate;
	box-sizing: content-box;
}

.logo-menu svg,
.line,
.navigation,
.nav-menu-shelf ul.abs li>a:after,
ul.pagination a li,
button:after,
button {
	transition: 0.5s all cubic-bezier(.87, -.41, .19, 1.44);
}


.nav-menu-shelf>* {
	opacity: 0;
	transition: 0.1s opacity ease-in-out, 0.5s transform ease-in-out;
	transform: translate(0px, 10px);
}

.nav-menu-shelf>* {
	opacity: 1;
	transition: 0.5s opacity ease-in-out, 0.5s transform ease-in-out;
	transition-delay: 0.35s;
	transform: translate(0px, 0px);
}

.nav-menu-shelf>*:nth-child(1) {
	transition-delay: 0.35s;
}

.nav-menu-shelf>*:nth-child(2) {
	transition-delay: 0.5s;
}

.nav-menu-shelf>*:nth-child(3) {
	transition-delay: 0.6s;
}

.nav-menu-shelf>*:nth-child(4) {
	transition-delay: 0.7s;
}

.nav-menu-shelf>*:nth-child(5) {
	transition-delay: 0.8s;
}

.nav-menu-shelf>*:nth-child(6) {
	transition-delay: 0.9s;
}

.nav-menu-shelf ul.social a li {
	transform: rotatey(180deg);
}

.nav-menu-shelf ul.social a li {
	transition: 0.5s transform ease-in-out;
	transform: rotatey(0deg);
	transition-delay: 0.6s;
}

.nav-menu-shelf ul.social li:before {
	transition: 0.3s color ease-in-out;
	transition-delay: 0s;
}








.logo-menu svg {
	transform: rotate(120deg);
	opacity: 1;
}

.logo-menu svg line {
	stroke-dashoffset: 100;
	animation: dash 0.3s linear forwards alternate;
	animation-delay: 0.15s;
}

.logo-menu svg.click:hover,
.hovered.click:hover {
  transform: rotate(120deg);
}

.lines {
	transform: rotate(60deg) translate(5.5px, -32.5px);
	opacity: 1;
	transition: 0.3s opacity ease-in-out;
	transition-delay: 0.3s;
}


.firstline,
.lastline,
.middleline {
	transition: 0.3s all cubic-bezier(.87, -.41, .19, 1.44);
}

.logo-menu svg:hover .firstline {
	transform: translatey(2px);
	transition-delay: 0.2s;
	opacity: 1;
}

.logo-menu svg.click .firstline {
	transform: rotate(45deg) translate(7px, -32px);
}

.logo-menu svg:hover .lastline {
	transform: translatey(-2px);
	transition-delay: 0.2s;
}

.logo-menu .lines {
	opacity: 1;
}

.logo-menu svg.click .lastline {
	transform: rotate(-45deg) translate(-26px, 21px);
}

.logo-menu svg.click:hover {
	transform: rotate(120deg);
}

.logo-menu svg.click .middleline {
	animation: undash 0.4s forwards linear;
}

.logo-menu svg.click {
	transform: rotate(120deg);
}

.logo-menu svg.click line {
	animation: dash 0.4s linear forwards alternate;
	animation-delay: 0.15s;
}

line {
	stroke: #000000;
	stroke-width: 4;
	stroke-linecap: round;
	stroke-miterlimit: 10;
	stroke-dasharray: 33;
	stroke-dashoffset: 50;
	animation: undash 0.4s forwards linear;
}


