/*Animations */
@keyframes fadeEffect {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes dash {
	from {
		stroke-dashoffset: 100;
	}
	to {
		stroke-dashoffset: 69;
	}
}

@keyframes undash {
	from {
		stroke-dasharray: 100;
		stroke-dashoffset: 0;
	}
	to {
		stroke-dasharray: 100;
		stroke-dashoffset: 102;
	}
}

@keyframes shelf {
	0% {
		padding-right: 0px;
	}
	20% {
		padding-right: 20px;
	}
	40% {
		padding-right: 10px;
	}
	60% {
		padding-right: 15px;
	}
	80% {
		padding-right: 5px;
	}
	100% {
		padding-right: 0px;
	}
}


