@keyframes crescendo {
	0%   {transform: scale(.8);}
	100% {transform: scale(1.2);}
  }

.view-case-study {
	background: rgba(255, 255, 255, 0.35);
	border-radius: 100px;
	position: absolute;
    margin-top: 85vh;
	border: 0;
	width: 65px;
	height: 65px;
    left: 80vw;
    /* top: 50%; */
	/* bottom: 60px;
	right: -100px; */
	cursor: pointer;
	z-index: 3;
	overflow: hidden;
	animation: crescendo 1.5s alternate infinite ease-in;
}

.view-case-study:before {
	display: block;
}

.view-case-study {
	z-index: 3;
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
	background: rgba(255, 255, 255, 1);
}

.view-case-study:hover:after {
	opacity: 0;
	background: transparent;
}

.view-case-study>span:last-child {
	margin: -1px 0 0;
	transform: rotate(45deg) translate3d(0, 0, 0);
}

.view-case-study>span {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 2px;
	height: 8px;
	margin: -6px 0 0;
	background-color: #000000;
	transform: rotate(-45deg) translate3d(0, 0, 0);
	transition: height 0.5s cubic-bezier(.666, 0, .333, 1), margin 0.5s cubic-bezier(.666, 0, .333, 1);
}

.view-case-study:hover>span,
.work-mode .view-case-study>span {
	background-color: #000000;
}

.view-case-study:hover,
.view-case-study:hover {
	background: rgba(255, 255, 255, 1);
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
}

.view-case-study:hover {
	transform: scale(1.2) translate3d(0, 0, 0);
}

.work-mode .view-case-study>span {
	transform: rotate(45deg) translate3d(0, 0, 0);
}

.work-mode .view-case-study>span:last-child {
	transform: rotate(-45deg) translate3d(0, 0, 0);
}

