.view-case-study-wiw.projects-load-wiw {
	right: 60px;
	transition: 0.5s all cubic-bezier(.87, -.41, .19, 1.44), 1.5s right cubic-bezier(.87, -.41, .19, 1.44) 0.15s;
}

.view-case-study-wiw {
	background: rgba(255, 255, 255, 0.35);
	border-radius: 100px;
    
	position: fixed;
    margin-top: 85vh;
	border: 0;
	width: 65px;
	height: 65px;
    left: 85vw;
    /* top: 50%; */
	/* bottom: 60px;
	right: -100px; */
	cursor: pointer;
	z-index: 3;
	overflow: hidden;
}
.view-case-study:before {
	display: block;
}

.view-case-study-wiw {
	z-index: 3;
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
	background: rgba(255, 255, 255, 1);
}

.view-case-study-wiw:hover:after {
	opacity: 0;
	background: transparent;
}

.view-case-study-wiw>span:last-child {
	margin: -1px 0 0;
	transform: rotate(-45deg) translate3d(0, 0, 0);
}

.view-case-study-wiw>span {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 2px;
	height: 8px;
	margin: -6px 0 0;
	background-color: #000000;
	transform: rotate(45deg) translate3d(0, 0, 0);
	transition: height 0.5s cubic-bezier(.666, 0, .333, 1), margin 0.5s cubic-bezier(.666, 0, .333, 1);
}

.view-case-study-wiw:hover>span,
.work-mode .view-case-study-wiw>span {
	background-color: #000000;
}

.view-case-study-wiw:hover,
.view-case-study-wiw:hover {
	background: rgba(255, 255, 255, 1);
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
}

.view-case-study-wiw:hover {
	transform: scale(1.2) translate3d(0, 0, 0);
}

.work-mode .view-case-study-wiw>span {
	transform: rotate(45deg) translate3d(0, 0, 0);
}

.work-mode .view-case-study-wiw>span:last-child {
	transform: rotate(-45deg) translate3d(0, 0, 0);
}

