.container {
	position: absolute;
	width: 100%;
	left: -2000px;
	padding: 50px;
	height: 100vh;
	-webkit-animation: slide 0.5s forwards;
    -webkit-animation-delay: 0.5s;
    animation: slide 0.5s forwards;
    animation-delay: 0.5s;
}

@keyframes slide {
    100% { left: 0; }
}

#hero h4 {
	position: absolute;
	margin-top: 25vh;
    font-family: DIN-LIGHT;
	font-size: 22px;
	letter-spacing: 0px;
	line-height: 34px;
	color: #000000;
}

#hero h1 {
	color: #000000;
	font-family: DIN;
	line-height: 80px;
	font-size: 80px;
	margin: 32vh 0px;
	width: 100%;
}

#hero h3 {
	position: absolute;
	font-family: DIN-LIGHT;
	font-size: 44px;
	margin: 42vh 0px;
	color: #808080;
}

.typingContainer {
    display: block;
    position: absolute;
    width: 60vw;
    /* left: 1px; */
}

.scrollsectionclass {
	position: relative;
	left: 45%;
}


.scrollsectionclass a{
	position: absolute;
	bottom: 20px;
	left: 50%;
	z-index: 2;
	display: inline-block;
	transform: translate(0, -50%);
	color: #d3d3d3;
	font: normal 400 16px/1 'Josefin Sans', sans-serif;
	letter-spacing: .1em;
	text-decoration: none;
	transition: opacity .3s;
}

.scrollsectionclass a:hover {
	opacity: .5;
}

#scrollid p{
    position: absolute;
	bottom: 140px;
	left: 50%;
    right: 50%;
	display: inline-block;
	transform: translate(0, -50%);
    margin-left: -23px;
    letter-spacing: .1em;
}

#scrollid a {
	padding-top: 90px;
	background-color: blueviolet;
	left: 50%;
	right: 50%;
}

#scrollid a span {
	position: absolute;
	top: 0;
	left: 50%;
	width: 30px;
	height: 50px;
	margin-left: -15px;
	border: 2px solid #d3d3d3;
	border-radius: 50px;
	box-sizing: border-box;
}

#scrollid a span::before {
	position: absolute;
	top: 10px;
	left: 50%;
	content: '';
	width: 6px;
	height: 6px;
	margin-left: -3px;
	background-color: #000000;
	border-radius: 100%;
	animation: scrollkeyframe 2s infinite;
	box-sizing: border-box;
}

@keyframes scrollkeyframe {
	0% {
		transform: translate(0, 0);
		opacity: 0;
	}
	40% {
		opacity: 1;
	}
	80% {
		transform: translate(0, 20px);
		opacity: 0;
	}
	100% {
		opacity: 0;
	}
}


